.profile-col {
	height: 83.0vh;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align:start;
	    align-items:flex-start;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	border-right: dotted 4px #cfd8dc;
	margin-top: 20px;
	margin-bottom: 20px;
}

.info-box {
	width: 100%;
	text-align: center;
}

.picture-box {
	max-width: 60%;
	height: auto;
	margin-top: 50px;
	border-radius: 100%;
}

.picture-box-container {
	text-align: center;
}



